.about-page {
  text-align: center;
  padding: 50px;
  min-height: 100vh;
  background: white; /* Light green */

}

/* üìå Back-EndÏôÄ Front-EndÎ•º ÎÇòÎûÄÌûà Î∞∞Ïπò */
.skills-container {
  align-items: flex-start;
  display: flex;
  gap: 80px;
  justify-content: center;
  margin: auto;
  max-width: 1200px;
  width: 100%;
  flex-wrap: wrap;
}

.backend-container, .frontend-container {
  width: 45%;
  min-width: 300px;
}

/* üìå ÌÉÄÏù¥ÌãÄ Ïä§ÌÉÄÏùº */
.about-page-h3 {
  font-size: 1.8rem;
  color: #fff;
  background-color: #4caf50;
  padding: 8px 20px;
  border-radius: 20px;
  display: inline-block;
  margin-bottom: 20px;
  font-weight: bold;
  letter-spacing: 1px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* üìå Î¶¨Ïä§Ìä∏Î•º 2Ïó¥Î°ú Î∞∞Ïπò */
.about-page-ul {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  width: 100%;
  justify-items: center;
}

.about-page-list-hover-si{
  font-size: 13px;
}

/* üìå Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.about-page-li {
  position: relative;
  background: linear-gradient(135deg, #f0f8ff, #e6f7ff);
  border: none;
  padding: 20px;
  border-radius: 15px;
  font-size: 1.2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
  min-width: 150px;
  height: 120px;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
  text-align: center;
}

/* üìå ÎßàÏö∞Ïä§ Ïò§Î≤Ñ Ìö®Í≥º */
.about-page-li:hover {
  background: #acefae;
  color: black;
  transform: scale(1.05);
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.3);
}

/* üìå ÏïÑÏù¥ÏΩò Ïä§ÌÉÄÏùº */
.AboutPage-Icons {
  margin-left: 10px;
  font-size: 35px;
  transition: transform 0.3s ease-in-out;
}

/* üìå ÏïÑÏù¥ÏΩò ÌùîÎì§Î¶¨Îäî Ïï†ÎãàÎ©îÏù¥ÏÖò */
.about-page-li:hover .AboutPage-Icons {
  transform: rotate(-10deg) scale(1.2);
}

/* üìå Î∞òÏùëÌòï: ÌÉúÎ∏îÎ¶øÏóêÏÑúÎäî Ìïú Ï§ÑÏóê 1Í∞úÏî© */
@media (max-width: 1024px) {
  .skills-container {
    flex-direction: column;
    align-items: center;
  }

  .backend-container, .frontend-container {
    width: 100%;
  }

  .about-page-ul {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* üìå Î™®Î∞îÏùºÏóêÏÑúÎäî 1Ïó¥ Î∞∞Ïπò */
@media (max-width: 768px) {
  .about-page-ul {
    grid-template-columns: repeat(1, 1fr);
  }
}

/* üìå Default Style */
.skill-description {
  font-size: 1.2rem;
  transition: font-size 0.3s ease-in-out, transform 0.3s ease-in-out;
  display: block; /* Ensures the transition applies properly */
}

/* üìå Shrink text on hover */
.about-page-li:hover .skill-description {
  font-size: 0.9rem; /* Shrink slightly */
  transform: scale(0.95); /* Optional: Adds a subtle shrinking effect */
}


/* üìå GitHub & NotionÏùÑ Ï§ëÏïô Ï†ïÎ†¨ */
.central-container {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-top: 30px;
}

.central-li {
  width: 300px;
  height: 100px;
  font-size: 1.4rem;
}

