/* ÌîÑÎ°úÏ†ùÌä∏ Ïπ¥Îìú ÏßÄÍ∑∏Ïû¨Í∑∏ Î∞∞Ïπò */
.project-card {
  display: flex;
  width: 90%;
  background: linear-gradient(135deg, #f0f8ff, #e6f7ff);
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 50px;
  overflow: hidden;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:nth-child(even) {
  flex-direction: row-reverse;
}

/* Ïπ¥ÎìúÏóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÏïΩÍ∞Ñ Ïª§ÏßÄÍ≥† Í∑∏Î¶ºÏûêÍ∞Ä Ïª§Ïßê */
.project-card:hover {
  transform: scale(1.02);
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
}

/* ÌîÑÎ°úÏ†ùÌä∏ Ïù¥ÎØ∏ÏßÄ Ïª®ÌÖåÏù¥ÎÑà */
.project-images {
  display: flex;
  flex-direction: column;
  width: 30%;
  align-items: center;
  padding: 10px;
  grid-gap: 25px;
}

/* Ïù¥ÎØ∏ÏßÄ Ïï†ÎãàÎ©îÏù¥ÏÖò */
.project-img, .project-img2, .WeworkHome {
  width: 100%;
  max-height: 200px;
  object-fit: contain;
  transition: transform 0.3s ease-in-out;
}

/* ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ Ïù¥ÎØ∏ÏßÄ ÌôïÎåÄ Ìö®Í≥º */
.project-img:hover, .project-img2:hover, .WeworkHome:hover {
  transform: scale(1.05);
}

/* ÌîÑÎ°úÏ†ùÌä∏ ÎÇ¥Ïö© */
.project-content {
  padding: 20px;
  flex: 1;
  position: relative;
}

/* Î≤ÑÌäº Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Ïà®Í∏∞Í∏∞ */
.project-page-button {
  margin-top: 20px;
  padding: 12px 30px;
  font-size: 18px;
  background-color: #76c64f;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-right: 10px;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

/* Ïπ¥ÎìúÏóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ Î≤ÑÌäºÏù¥ ÎÇòÌÉÄÎÇ® */
.project-card:hover .project-page-button {
  opacity: 1;
  transform: translateY(0);
}

/* Î≤ÑÌäº Ìò∏Î≤Ñ Ìö®Í≥º */
.project-page-button:hover {
  background-color: #074e0d;
  transform: scale(1.1);
}
